import { Column } from '@codecademy/gamut/src';
import { styled } from '@storybook/theming';
import { colors } from '@codecademy/gamut-styles/utils/variables';

// The normal import would be '@storybook/addon-links/react'
// but it is throwing a typescript error in the current alpha
import LinkTo from '@storybook/addon-links/dist/react/components/link';
import React from 'react';

export type Section = 'Atoms' | 'Molecules' | 'Organisms' | 'Foundations';

export type ExampleShape = {
  name: string;
  kind: string;
  text?: string;
};

export type AboutCellProps = {
  emoji: string;
  examples?: ExampleShape[];
  label?: string;
  kind: Section;
  story?: string;
  title?: string;
  category?: string;
};

const Heading = styled.h2`
  border-bottom: 1px solid ${colors.gray[200]};
  color: ${colors.gray[600]};
  font-size: 1.5rem;
  margin-bottom: 1rem;
  padding-bottom: 0.25rem;
`;

const Text = styled.p`
  font-size: 0.8rem;
`;

export const AboutCell: React.FC<AboutCellProps> = ({
  children,
  emoji,
  examples,
  kind,
  category,
  story = 'About',
  label = 'Examples',
  title = kind || category,
}) => {
  const kindLink = [category, kind].filter(Boolean).join('|');
  return (
    <Column size={{ xs: 12, sm: 6, md: 4 }}>
      <div>
        <Heading>
          <LinkTo kind={kindLink} story={story}>
            <span role="presentation">{emoji}</span> {title}
          </LinkTo>
        </Heading>
        <Text>{children}</Text>
        {examples && (
          <Text>
            {label}:{' '}
            {examples.map((example, i) => {
              const exampleLink = [category, example.kind]
                .filter(Boolean)
                .join('|');
              return (
                <React.Fragment key={example.name}>
                  <LinkTo kind={exampleLink} story={example.name}>
                    {example.text || example.name}
                  </LinkTo>
                  {i !== examples.length - 1 && ', '}
                </React.Fragment>
              );
            })}
          </Text>
        )}
      </div>
    </Column>
  );
};

export default AboutCell;
try {
    // @ts-ignore
    AboutCell.displayName = "AboutCell";
    // @ts-ignore
    AboutCell.__docgenInfo = { "description": "", "displayName": "AboutCell", "props": { "emoji": { "defaultValue": null, "description": "", "name": "emoji", "required": true, "type": { "name": "string" } }, "examples": { "defaultValue": null, "description": "", "name": "examples", "required": false, "type": { "name": "ExampleShape[]" } }, "label": { "defaultValue": { value: "Examples" }, "description": "", "name": "label", "required": false, "type": { "name": "string" } }, "kind": { "defaultValue": null, "description": "", "name": "kind", "required": true, "type": { "name": "enum", "value": [{ "value": "\"Atoms\"" }, { "value": "\"Molecules\"" }, { "value": "\"Organisms\"" }, { "value": "\"Foundations\"" }] } }, "story": { "defaultValue": { value: "About" }, "description": "", "name": "story", "required": false, "type": { "name": "string" } }, "title": { "defaultValue": { value: "kind || category" }, "description": "", "name": "title", "required": false, "type": { "name": "string" } }, "category": { "defaultValue": null, "description": "", "name": "category", "required": false, "type": { "name": "string" } } } };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["Blocks/AboutCell.tsx#AboutCell"] = { docgenInfo: AboutCell.__docgenInfo, name: "AboutCell", path: "Blocks/AboutCell.tsx#AboutCell" };
}
catch (__react_docgen_typescript_loader_error) { }